<template>
  <div class="container">
    <img :src="require(`./${id}.png`)" width="100%" />
    <div style="text-align: center; margin-top: 20px">
      <audio ref="audio" :src="audioSrc" controls></audio>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      id: 0,
      list: [],
    };
  },
  computed: {
    audioSrc() {
      return this.list.find((item) => item.id === this.id).audioSrc;
    },
  },
  created() {
    const { id } = this.$route.query;
    this.id = +id;
    this.list = require("./list.json");
  },
  methods: {},
};
</script>
<style lang="scss" scoped></style>
